import { Typography, Image, Flex, Button, Col, Row, Card, Avatar, Space } from "antd";
import styled from "@emotion/styled";
import Slider from "./Slider";
import CardComponent from "./CardComponent";
import {
  ArrowRightOutlined,
  VideoCameraAddOutlined,
  FilePdfOutlined,
  FieldTimeOutlined,
  UserOutlined,
  QuestionCircleOutlined,
  AimOutlined,
  FireOutlined,
  FormOutlined,
} from "@ant-design/icons";
import DividerComponent from "./DividerComponent";
import { Link } from "react-router-dom";

const StyledComponent = styled.div`
  .ant-carousel {
    margin-bottom: 4em;
  }
  .about-me {
    img {
      border-radius: 50%;
    }
    .text-content {
      line-height: 15px;
      margin-left: auto;
      .ant-typography {
        text-align: justify;
      }
    }
  }
  .story-card {
    background: #efefef;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 90%;

    .ant-card-body {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  .speciality {
    min-height: 200px;
    .ant-card {
      height: 100%;
    }
    .anticon {
      font-size: 2.8em;
    }
    .ant-typography {
      margin-left: 2em;
      text-align: justify;
    }
  }
  .course-card {
    .ant-card {
      background: transparent;
    }
  }
`;

const { Title, Text } = Typography;

